import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import author from '@/components/author'
Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld,
      meta: {
        auth: true // 如果此路由需要微信授权请设置为true,默认为false
      }
    },{
     path:'/author',
      name:'author',
      component:author,
    }
  ]
})



