//     var a= location.href;
//      if (!this.$store.getters.token) {
//        if (!urlSearch.has("code")) {
////            url = a+'&response_type=code&scope=snsapi_userinfo&state=STATE#wechat_redirect';
////            urls='https://open.weixin.qq.com/connect/oauth2/authorize?appid='+appid +'&redirect_uri='+encodeURIComponent(url);
//
//        location.href = urls;
//      } else {
//        const code = urlSearch.get("code");
//        time = new Date(urlSearch.get("state")).toLocaleDateString();
//
